export default {
  data() {
    return {
      isResizing: false,
      startX: 0,
      deltaX: 0,
      cols1: 6,
      cols2: 6,
      vertical: false,
    };
  },
  methods: {
    startResize() {
      this.isResizing = true;
      document.addEventListener("mousemove", this.handleMouseMove);
      document.addEventListener("mouseup", this.handleMouseUp);
    },
    handleMouseMove(event) {
      if (this.isResizing) {
        const middle = event.clientX;

        const ratio = middle / window.innerWidth;
        this.cols1 = Math.max(1, Math.floor(ratio * 12));
        this.cols2 = 12 - this.cols1;

        const middleY = event.clientY;
        const ratioY = middleY / window.innerHeight;

        if (ratioY > 0.5) {
          this.vertical = true;
          this.cols1 = 12;
          this.cols2 = 12;
        } else {
          this.vertical = false;
        }
      }
    },
    handleMouseUp() {
      this.isResizing = false;
      document.removeEventListener("mousemove", this.handleMouseMove);
      document.removeEventListener("mouseup", this.handleMouseUp);

      const preference = JSON.stringify({
        cols1: this.cols1,
        cols2: this.cols2,
        vertical: this.vertical,
      });
      localStorage.setItem("scheduleLayout", preference);
    },
    loadSavedPreference() {
      const preferenceJson = localStorage.getItem("scheduleLayout");
      if (preferenceJson == null) {
        return;
      }

      const preference = JSON.parse(preferenceJson);
      this.cols1 = preference.cols1;
      this.cols2 = preference.cols2;
      this.vertical = preference.vertical;
    },
  },
};
