<template>
  <v-card class="mx-auto mt-5">
    <v-dialog v-model="dialog">
      <configuracoes-geracao
        :id-configuracao="id"
        @close-dialog="dialog = false"
      />
    </v-dialog>
    <v-row :class="vertical ? null : 'flex-nowrap'">
      <v-col
        :cols="$store.state.selectedSchedule ? cols1 : null"
        v-show="cols1 > 0"
      >
        <lista-horarios
          :id="id"
          @view-schedule="viewSchedule"
          @generate="generate"
          @open-dialog="dialog = true"
          ref="listaHorarios"
        />
      </v-col>
      <v-col :cols="cols2" v-show="cols2 > 0 && $store.state.selectedSchedule">
        <tabela-horario
          :aulasPorDia="aulasPorDiaTurnoSelecionado"
          :professores="professores"
          :id="id"
          :id-horario="id_horario"
          @next-schedule="nextSchedule"
          @prev-schedule="previousSchedule"
          @resize="startResize"
          @reload="load"
        />
      </v-col>
    </v-row>
  </v-card>
</template>

<script>
import helper from "@/mixins/helper";
import resize from "@/mixins/resize";
export default {
  name: "Horarios",
  props: ["id"],
  mixins: [helper, resize],
  components: {
    TabelaHorario: () => import("@/components/TabelaHorario.vue"),
    ListaHorarios: () => import("@/components/ListaHorarios.vue"),
    ConfiguracoesGeracao: () => import("@/components/ConfiguracoesGeracao.vue"),
  },
  data() {
    return {
      id_horario: null,
      dialog: false,
    };
  },
  mounted() {
    this.loadSavedPreference();
  },
  methods: {
    async generate() {
      this.$refs.fila.startInterval();
    },
    viewSchedule(id_horario) {
      this.id_horario = id_horario;
    },
    nextSchedule() {
      this.$refs.listaHorarios.nextSchedule();
    },
    previousSchedule() {
      this.$refs.listaHorarios.previousSchedule();
    },
    beforePageExit() {
      this.$refs.listaHorarios.stopInterval();
    },
    load() {
      this.$refs.listaHorarios.load();
    },
  },
};
</script>

<style></style>
